body {
  background: #fff6d3;
  margin: 0;
  padding: 0;
  font-family: BirdSeed;
}

#root {
  width: 320px;
  margin: 30px auto 50px;
}

#title {
  margin-left: 20px;
}

#title .gex-plays {
  width: 45px;
  font-size: 26px;
  transform: rotate(-10deg);
}

#title h2 {
  text-align: center;
  font-family: Curse;
  font-weight: bold;
}

#title h1 {
  text-align: center;
  font-family: BubbleTime;
}

#title h2 span {
  margin-left: 15px;
  margin-right: 15px;
}

.player-view .captured-pieces {
  position: absolute;
  bottom: 0;
}

@font-face {
  font-family: BirdSeed;
  src: url("birdseed.1ab5f1c6.ttf") format("truetype");
}

@font-face {
  font-family: BubbleTime;
  src: url("bubble-time.bc2575ef.ttf") format("truetype");
}

@font-face {
  font-family: Curse;
  src: url("curse.f3c8bd3c.ttf") format("truetype");
}

[data-square] div div {
  font-family: BirdSeed !important;
}

a, a:visited {
  color: #7c3f58;
  text-decoration-style: dotted;
}

.modal {
  width: 200px;
  height: 200px;
  color: #7c3f58;
  width: 290px;
  z-index: 999;
  text-align: center;
  background: #fff6d3;
  border: 2px solid #7c3f58;
  margin: 20px;
  display: inline-block;
  position: absolute;
  top: 220px;
  left: 27vw;
  overflow: hidden;
}

button {
  color: #7c3f58;
  cursor: pointer;
  background: #fff6d3;
  border: 2px solid #7c3f58;
  border-radius: 2px;
  padding: 10px;
  font-family: BirdSeed;
  font-weight: bold;
}

button:hover {
  color: #fff6d3;
  background: #f9a974;
}

.corner {
  width: 6em;
  height: 6em;
  opacity: 1;
  background: none;
  font-size: 10px;
  transition: opacity .3s ease-in-out;
  position: absolute;
}

.corner:after {
  content: "";
  width: .2em;
  height: .2em;
  display: block;
  position: absolute;
}

.topleft {
  top: 1em;
  left: 1em;
  transform: rotate(360deg);
}

.topright {
  top: 1em;
  right: 1em;
  transform: rotate(90deg);
}

.bottomleft {
  bottom: 1em;
  left: 1em;
  transform: rotate(270deg);
}

.bottomright {
  bottom: 1em;
  right: 1em;
  transform: rotate(180deg);
}

.variant:after {
  width: .1em;
  height: .1em;
}

